import { useState, useEffect, useCallback } from "react";
import { useHistory } from "react-router-dom";
import moment from "moment";
import { Hidden, IconButton, Collapse } from "@material-ui/core";
import { ArrowBackIos } from "@material-ui/icons";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import {
  DialogTitle,
  DialogContent,
  DialogActions,
  Dialog,
  Button,
} from "@mui/material";

import { setupFreeBusiness } from "../../../api/preBusiness";

import { UIButton } from "../../exports";
import { useToastMessage } from "../../../contexts";

import { loggedUserDetail } from "../../../api/Auth";
import { CC_AVENUE_URL, RAZERPAY_KEY } from "Backend";
import RazorpayAPI from "api/razorpay";
const currentDate = moment(new Date()).format("Do MMM");
const date = new Date();
const lastDate = moment(
  new Date(date.getFullYear(), date.getMonth() + 1, 0)
).format("Do MMM");

export default function OrderSummary(props) {
  const { busiInfo } = props;
  const history = useHistory();

  const toastMessage = useToastMessage();

  const [, setUserDetail] = useState(null);

  const [billingData, setBillingData] = useState({
    companyName: "",
    address: "",
    city: "",
    state: "",
    pincode: "",
    gstNumber: "",
    panNumber: "",
    email: "",
    mobileNumber: "",
  });
  const { companyName, address, city, state, pincode, gstNumber } = billingData;
  const [removePlanId, setRemovePlanId] = useState(null);
  const [plans, setPlans] = useState([]);
  const [totalAmount, setTotalAmount] = useState(0);
  const [taxableAmount, setTaxableAmount] = useState(0);

  //payment form

  // payment form

  const [gstAmt, setGstAmt] = useState(0);
  const [grandTotal, setGrandTotal] = useState(0);
  const [, setDiscount] = useState("");
  const [promoCode, setPromoCode] = useState("");
  const [paymentStatus] = useState(null);
  const [udf2, setUdf2] = useState();
  const [loading, setLoading] = useState(false);

  const clearDiscountHandler = () => {
    setDiscount("");
    setPromoCode("");
    let discount = 0;
    calculatePrice(totalAmount, discount);
  };

  //dialog state for delete
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
  //funtion for opening confirm box for delete
  const handleClickOpen = () => {
    setOpenConfirmDialog(true);
  };

  //funtion for closing confirm box for delete
  const handleClickClose = () => {
    setOpenConfirmDialog(false);
  };

  const removePlan = async () => {
    let planSlugs = [];

    const planDetail = JSON.parse(localStorage.getItem("selectedPlanDetail"));

    const selectedPlanDetail = planDetail.filter(
      (plan) => plan.id !== removePlanId
    );

    localStorage.setItem(
      "selectedPlanDetail",
      JSON.stringify(selectedPlanDetail)
    );

    const updatedPlan = plans.filter((plan) => plan.id !== removePlanId);
    setPlans(updatedPlan);
    let basic_Amt = 0;
    let discount = 0;
    setDiscount("");
    setPromoCode("");
    for (var i = 0; i < updatedPlan.length; i++) {
      basic_Amt += updatedPlan[i]["amount"];
      planSlugs.push(updatedPlan[i]["slug"]);
    }
    calculatePrice(basic_Amt, discount);
    setUdf2(planSlugs);
  };

  const loadLoggedUserDetail = async () => {
    const res = await loggedUserDetail();
    if (!res.success) setUserDetail(res.payload);
  };

  useEffect(() => {
    loadLoggedUserDetail();
  });

  useEffect(() => {
    const plans = JSON.parse(localStorage.getItem("selectedPlanDetail"));
    setPlans(plans);

    setBillingData({
      ...billingData,
      companyName: busiInfo.billing_detail.company_name,
      address: busiInfo.billing_detail.address,
      city: busiInfo.billing_detail.city,
      state: busiInfo.billing_detail.state,
      pincode: busiInfo.billing_detail.pin_code,
      gstNumber: busiInfo.billing_detail.gst_number,
      panNumber: busiInfo.billing_detail.pan_number,
      country: busiInfo.billing_detail.country,
      mobileNumber: busiInfo.billing_detail.number,
      email: busiInfo.email,
    });

    let basic_Amt = 0;
    let discount = 0;
    let planSlugs = [];
    for (var i = 0; i < plans.length; i++) {
      basic_Amt += plans[i]["amount"];
      planSlugs.push(plans[i]["slug"]);
    }
    calculatePrice(basic_Amt, discount);
    setUdf2(planSlugs);
  }, []);

  const calculatePrice = async (totalAmt, discount) => {
    let taxAmount = totalAmt - discount;
    if (taxAmount < 1) taxAmount = 0;

    let gstAmt = taxAmount * 0.18;

    setGstAmt(gstAmt);
    setTaxableAmount(taxAmount);
    setTotalAmount(totalAmt);
    setGrandTotal(taxAmount + gstAmt);
  };

  if (paymentStatus === "success") {
    history.push({
      pathname: `/payment-success`,
    });
  }

  if (paymentStatus === "falied") {
    history.push({
      pathname: `/payment-failed`,
    });
  }

  const formatAmount = (amount) => {
    let amt = amount;
    return amt
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  const formatTwoPlace = (amount) => {
    return amount
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  // const validatePromo = async () => {
  //   let promoCode = promoInput;
  //   const res = await PromoCodeAPI.validatePromoCode({
  //     promocode: promoCode,
  //     total_amount: totalAmount,
  //   });
  //   if (!res.success) {
  //     toastMessage.setErrorMessage(res.message);
  //   } else {
  //     let discount;
  //     // if (res.promo.type === "flat") {
  //     discount = res.payload.promo_discount;
  //     //} else {
  //     //  discount = res.payload.promo_amount;
  //     //}
  //     setDiscount(discount);
  //     setPromoCode(promoCode);
  //     calculatePrice(totalAmount, discount);
  //   }
  //   /* if (promoCode > 5) {
  //     return toastMessage.setErrorMessage("Invalid promo");
  //   } */
  // };

  const backToBilling = () => {
    props.changeStep(props.editBillingStep);
    props.editBilling(true);
  };

  const createFreeBusiness = async () => {
    const res = await setupFreeBusiness({
      grandTotal,
      discount: 0,
      planIds: udf2,
    });

    if (res.success) {
      window.location.href = `/payment-success?id=${res.payload}`;
    } else {
      return toastMessage.setErrorMessage(res.message);
    }
  };

  const [show, setShow] = useState([false, false]);

  const showPlanDetail = (index1) => {
    let accordian = show.map((s, index) => {
      if (index === index1) {
        return (show[index] = true);
      } else {
        return (show[index] = false);
      }
    });
    setShow(accordian);
  };

  //TODO: TO be used on ccavenue
  // const preload = useCallback(async () => {
  //   if (parseFloat(gstAmt) + parseFloat(taxableAmount.toFixed(2)) > 0) {
  //     const res = await getHash({
  //       billing_name: billingData.name,
  //       billing_address: billingData.address,
  //       billing_city: billingData.city,
  //       billing_state: billingData.state,
  //       billing_zip: billingData.pincode?.toString(),
  //       billing_country: billingData.country?.toString(),
  //       billing_tel: billingData.mobileNumber?.toString(),
  //       billing_email: billingData.email?.toString(),
  //       amount: parseFloat(gstAmt) + parseFloat(taxableAmount.toFixed(2)),
  //       merchant_param1: localStorage.getItem("businessId"),
  //       merchant_param2: udf2?.join(","),
  //       merchant_param3: billingData.gstNumber,
  //       merchant_param4: billingData.panNumber,
  //     });
  //     setHash(res.payload);
  //   }
  // }, [
  //   gstAmt,
  //   taxableAmount,
  //   billingData.name,
  //   billingData.address,
  //   billingData.city,
  //   billingData.state,
  //   billingData.pincode,
  //   billingData.country,
  //   billingData.number,
  //   billingData.email,
  //   billingData.gstNumber,
  //   billingData.panNumber,
  //   udf2,
  // ]);
  useEffect(() => {
    const script = document.createElement("script");
    script.src = "https://checkout.razorpay.com/v1/checkout.js";
    script.async = true;
    document.body.appendChild(script);
  }, []);

  const openPayModal = async () => {
    setLoading(true);
    const res = await RazorpayAPI.createOrder({
      amount: parseFloat(gstAmt) + parseFloat(taxableAmount.toFixed(2)),
      notes: {
        businessId: localStorage.getItem("businessId"),
        plans: udf2?.join(","),
        gst: billingData.gstNumber,
        pan: billingData.panNumber,
      },
    });

    const options = {
      key: RAZERPAY_KEY,
      name: "Easy Social",
      description: plans[0]?.name,
      order_id: res.payload.order.id,
      prefill: {
        name: billingData.name,
        email: billingData.email,
        contact: billingData.number,
      },
      notes: {
        billing_name: billingData.name,
        billing_address: billingData.address,
        billing_city: billingData.city,
        billing_state: billingData.state,
        billing_zip: billingData.pincode?.toString(),
        billing_country: billingData.country,
        billing_tel: billingData.mobileNumber?.toString(),
        billing_email: billingData.email,
      },
      theme: {
        color: "#686CFD",
      },
      modal: {
        confirm_close: true,
        ondismiss: function () {
          setLoading(false);
        },
      },
      callback_url: `https://api.easysocial.in${res.payload.url}`,
      redirect: true,
      handler: function () {
        setLoading(false);
      },
    };

    const rzp1 = new window.Razorpay(options);
    rzp1.open();
  };

  //TODO: TO be used on ccavenue
  // useEffect(() => {
  //   preload();
  // }, [billingData, preload]);
  return (
    <>
      <Hidden mdUp implementation="js">
        <div className="form-header">
          <IconButton onClick={() => history.goBack()}>
            <ArrowBackIos />
          </IconButton>
          <div className="header-text">
            <h1 dangerouslySetInnerHTML={{ __html: props.title }} />
          </div>
        </div>
      </Hidden>
      <form onSubmit={(e) => e.preventDefault()}>
        <div className="fields">
          <div className="col-10 col-sm-6 col-md-12">
            <h3>{"Order Summary"}</h3>

            <div className="details">
              <div className="head">
                <h5 className="heading">{"Details"}</h5>
                <small className="duration">{`${currentDate} to ${lastDate}`}</small>
              </div>

              {plans &&
                plans.map((plan) => {
                  return (
                    <div className="particulars">
                      <p className="title">{`${plan.category} - ${plan.name} - ${plan.validity} Days`}</p>
                      <p className="amount">{`${formatTwoPlace(
                        plan.amount
                      )}`}</p>
                    </div>
                  );
                })}

              <div className="particulars">
                {promoCode !== "" && (
                  <div className={"discount-card"}>
                    <small>{String(promoCode).toUpperCase()}</small>
                    <button type="button" onClick={clearDiscountHandler}>
                      <svg
                        width="10"
                        height="10"
                        viewBox="0 0 10 10"
                        fill="none"
                        xmlns="//www.w3.org/2000/svg"
                      >
                        <g opacity="0.7">
                          <path d="M1 1L9.5 9.5" stroke="#24272C" />
                          <path d="M9.5 1L1 9.5" stroke="#24272C" />
                        </g>
                      </svg>
                    </button>
                  </div>
                )}
              </div>
              <div className="particulars">
                <p className="title">{"Taxable Amount"}</p>
                <p className="amount">{`${formatTwoPlace(taxableAmount)}`}</p>
              </div>
              <div className="particulars">
                <p className="title">{"GST Amount"}</p>
                <p className="amount">{`${formatTwoPlace(gstAmt)}`}</p>
              </div>
              <div className="particulars">
                <p className="title">{"GST No."}</p>
                <p className="number">{gstNumber}</p>
              </div>
              <hr />
              <div className="particulars">
                <p className="title">
                  <strong>{"Total Payable Amount"}</strong>
                </p>
                <p className="amount">
                  <strong>{`${formatTwoPlace(grandTotal)}`}</strong>
                </p>
              </div>
            </div>
          </div>
          <div className="col-10 col-sm-6 col-md-12">
            <div className="box-wrapper">
              <div className="billing-header">
                <h5 className="billing-title">{companyName}</h5>
                <button
                  type="button"
                  className="edit-action"
                  onClick={() => backToBilling()}
                >
                  {"Edit Billing Address"}
                </button>
              </div>
              <div className="billing-desc-wrapper">
                <div className="billing-desc">
                  {`${address}, ${city}, ${state}-${pincode}`}
                </div>
              </div>
            </div>
          </div>
          <div className="col-10 col-sm-6 col-md-12">
            <div className="box-wrapper">
              <h5 className="box-title">{"Plan Detail"}</h5>
              {plans &&
                plans.map((plan, index) => {
                  const price = formatAmount(plan.amount);
                  const planDetail = plan.plan_detail.split("\n");

                  return (
                    <div className="plan-box">
                      <div className="header">
                        <div className="info">
                          <h6 className="plan-name">{plan.title}</h6>

                          <small className="price">{`${price}/Month`}</small>
                        </div>
                        <div className="action">
                          {plans.length > 1 && (
                            <button
                              type="button"
                              onClick={() => {
                                setRemovePlanId(plan.id);
                                handleClickOpen();
                              }}
                            >
                              Remove
                            </button>
                          )}
                          <button
                            onClick={() => showPlanDetail(index)}
                            type="button"
                          >
                            {"Details "}
                            {show[index] ? (
                              <ExpandLessIcon style={{ fontSize: "16px" }} />
                            ) : (
                              <ExpandMoreIcon style={{ fontSize: "16px" }} />
                            )}
                          </button>
                        </div>
                      </div>
                      <Collapse in={show[index]} className="data">
                        <ul>
                          {planDetail.map((feature, index) => (
                            <li
                              key={index}
                              className={feature.includes("*") ? "star" : ""}
                            >
                              {feature.includes("*")
                                ? feature.slice(1)
                                : feature}
                            </li>
                          ))}
                        </ul>
                      </Collapse>
                    </div>
                  );
                })}
            </div>
          </div>
          <div className="col-10 col-sm-6 col-md-12 Submit_Container">
            {/* // <form action={CC_AVENUE_URL} name="redirect" method="post"> */}
            {/* <input
                type="hidden"
                id="encRequest"
                name="encRequest"
                value={hash}
              />

              <input
                type="hidden"
                name="access_code"
                id="access_code"
                value="AVEU27JI66CC36UECC"
              /> */}
            <UIButton
              disabled={loading}
              type="submit"
              variant="contained"
              className="mt-5 ms-0 pay-btn"
              onClick={openPayModal}
              showLoader={loading}
            >
              {`Pay ${formatAmount(grandTotal)}`}
            </UIButton>
          </div>
        </div>
      </form>

      <Dialog
        open={openConfirmDialog}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">Delete</DialogTitle>
        <DialogContent>Are you sure you want to delete?</DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              handleClickClose();
            }}
          >
            Cancel
          </Button>
          <Button
            onClick={async () => {
              await removePlan();
              handleClickClose();
            }}
            autoFocus
          >
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
