import React, { Component } from "react";
import { Map, Marker, GoogleApiWrapper } from "google-maps-react";
import Box from "@material-ui/core/Box";
import SearchBox from "../../../../assets/icons/search.svg";
//import "./global/map.css";

import "./global/map.css";
//import "./global/map.scss";

import PlacesAutocomplete, {
  geocodeByAddress,
  getLatLng,
} from "react-places-autocomplete";

import CrossBox from "../../../../assets/icons/cross-box.svg";

export class MapContainer extends Component {
  constructor(props) {
    super(props);
    let latitude = 49.2827291;
    let longitude = -123.1207375;
    console.log(props.latlong);
    if (props.latlong) {
      latitude = props.latlong.lat;
      longitude = props.latlong.lng;
    }

    this.state = {
      // for google map places autocomplete
      address: "",
      showingInfoWindow: false,
      activeMarker: {},
      selectedPlace: "",
      mapCenter: {
        lat: latitude,
        lng: longitude,
      },
    };
  }

  handleChange = (address) => {
    this.setState({ address });
  };

  handleClearInput = () => {
    this.setState({ address: "" });
    this.setState({ selectedPlace: "" });
  };
  handleSelect = (address) => {
    this.setState({ address });
    this.setState({ selectedPlace: address });
    geocodeByAddress(address)
      .then((results) => getLatLng(results[0]))
      .then((latLng) => {
        // update center state
        this.setState({ mapCenter: latLng });
        this.props.setLatlong(this.state.mapCenter);
      })
      .catch((error) => console.error("Error", error));
  };
  dragEndHandler = (one, two, three) => {
    const { latLng } = three;
    const lat = latLng.lat();
    const lng = latLng.lng();
    this.setState({
      mapCenter: {
        lat,
        lng,
      },
    });
    this.props.setLatlong(this.state.mapCenter);
  };

  render() {
    const { forBusiness = false } = this.props;

    return (
      <div id="googleMaps">
        <PlacesAutocomplete
          value={this.state.address}
          onChange={this.handleChange}
          onSelect={this.handleSelect}
        >
          {({
            getInputProps,
            suggestions,
            getSuggestionItemProps,
            loading,
          }) => {
            return (
              <div className="location-search-wrapper">
                <div className="search-box">
                  <img
                    src={SearchBox}
                    className="search-box-icon"
                    alt="Search icon"
                  />
                </div>
                {this.state.address.length > 0 && (
                  <div className="cross-box" onClick={this.handleClearInput}>
                    <img
                      src={CrossBox}
                      className="cross-box-icon"
                      alt="Cross icon"
                    />
                  </div>
                )}
                <input
                  {...getInputProps({
                    placeholder: "Search Location or Mark Pointer",
                    className:
                      suggestions.length > 0
                        ? "location-search-input-active"
                        : "location-search-input",
                  })}
                />

                <div className="autocomplete-dropdown-container">
                  {loading && <div className="loadingWrapper">Loading...</div>}
                  {suggestions.map((suggestion, index) => {
                    const className = suggestion.active
                      ? "suggestion-item--active"
                      : "suggestion-item";
                    // inline style for demonstration purpose
                    const style = suggestion.active
                      ? { backgroundColor: "#fafafa", cursor: "pointer" }
                      : { backgroundColor: "#ffffff", cursor: "pointer" };
                    return (
                      <div
                        {...getSuggestionItemProps(suggestion, {
                          className,
                          style,
                        })}
                        key={index}
                      >
                        <span className="main-title">
                          {suggestion.formattedSuggestion.mainText}
                        </span>
                        {suggestion.formattedSuggestion.secondaryText && (
                          <p className="main-subtitle">
                            {suggestion.formattedSuggestion.secondaryText}
                          </p>
                        )}
                      </div>
                    );
                  })}
                </div>
              </div>
            );
          }}
        </PlacesAutocomplete>
        <div className="map-wrapper">
          <Box display={{ xs: "none", md: "block" }}>
            <Map
              containerStyle={{
                position: "relative",
                width: "0px",
                height: "0px",
              }}
              style={{
                position: "relative",
                width: "318px",
                height: forBusiness ? "370px" : "448px",
                borderRadius: "12px",
              }}
              google={this.props.google}
              options={{
                fullscreenControl: false,
              }}
              initialCenter={{
                lat: this.state.mapCenter.lat,
                lng: this.state.mapCenter.lng,
              }}
              center={{
                lat: this.state.mapCenter.lat,
                lng: this.state.mapCenter.lng,
              }}
            >
              <Marker
                draggable={true}
                onDragend={(t, map, coord) =>
                  this.dragEndHandler(t, map, coord)
                }
                position={{
                  lat: this.state.mapCenter.lat,
                  lng: this.state.mapCenter.lng,
                }}
              />
            </Map>
          </Box>
          <Box display={{ xs: "block", md: "none" }}>
            <Map
              containerStyle={{
                position: "relative",
                width: "100%",
                height: "100%",
              }}
              style={{
                position: "relative",
                width: "100%",
                height: "370px",
                borderRadius: "12px",
              }}
              google={this.props.google}
              options={{
                fullscreenControl: false,
              }}
              initialCenter={{
                lat: this.state.mapCenter.lat,
                lng: this.state.mapCenter.lng,
              }}
              center={{
                lat: this.state.mapCenter.lat,
                lng: this.state.mapCenter.lng,
              }}
            >
              <Marker
                draggable={true}
                position={{
                  lat: this.state.mapCenter.lat,
                  lng: this.state.mapCenter.lng,
                }}
                onDragend={(t, map, coord) =>
                  this.dragEndHandler(t, map, coord)
                }
              />
            </Map>
          </Box>
        </div>
      </div>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: "AIzaSyCJikCEsC6sImWdw2Q97SWsS7145s-TtLo",
})(MapContainer);
