import axios from "axios";
import { useState, useEffect, useCallback } from "react";
import { useHistory } from "react-router-dom";
import {
  Hidden,
  IconButton,
  Select,
  OutlinedInput,
  MenuItem,
} from "@material-ui/core";
import { ArrowBackIos } from "@material-ui/icons";
import { UIButton } from "../../../Components/exports";
import { useToastMessage } from "../../../contexts";

import Map from "./Map/map";
// import Map from "./Map";
import { saveLocation } from "../../../api/preBusiness";
import { timeZoneList } from "../../../utils/GeneralSettingsData";

export default function LocationSetup({ changeStep, title }) {
  //const [latlong, setLatlong] = useState(null);
  const history = useHistory();
  const toastMessage = useToastMessage();

  /*  const [fields, setFields] = useState({
    timezoneLabel: "(GMT+05:30) Chennai, Kolkata, Mumbai, New Delhi",
    timezoneValue: "5.5",
    latlong:""
  }); */

  const [fields, setFields] = useState({
    timezoneLabel: "",
    timezoneValue: "",
    latlong: "",
    latitude: "",
    longitude: "",
  });

  const { timezoneLabel, timezoneValue, latlong, latitude, longitude } = fields;

  const getCountry = async () => {
    try {
      let countryCode = "IN";
      const visitorReq = await axios.get("https://geolocation-db.com/json/");
      console.log(visitorReq.data);
      if (visitorReq.status === 200) {
        countryCode = visitorReq.data.country_code;
      }
      const timeZoneData = await timeZoneList.filter(
        (timezone) => timezone.country_code === countryCode
      );
      setFields({
        ...fields,
        timezoneLabel: timeZoneData[0]["label"],
        timezoneValue: timeZoneData[0]["value"],
      });
    } catch (error) {
      let countryCode = "IN";
      const timeZoneData = await timeZoneList.filter(
        (timezone) => timezone.country_code === countryCode
      );
      setFields({
        ...fields,
        timezoneLabel: timeZoneData[0]["label"],
        timezoneValue: timeZoneData[0]["value"],
      });
    }
  };

  useEffect(() => {
    getCountry();
  }, []);

  const handleTimezoneValue = (e) => {
    let value = e.target.value;
    const selectTimeZone = timeZoneList.filter((e) => e.label === value);
    setFields({
      ...fields,
      timezoneValue: selectTimeZone[0]["value"],
      timezoneLabel: value,
    });
  };

  const setLatlong = (value) => {
    let latitude = value.lat;
    let longitude = value.lng;
    setFields({ ...fields, latitude: latitude, longitude: longitude });
  };

  const submit = async (e) => {
    e.preventDefault();
    const data = {
      timezone: {
        label: timezoneLabel,
        value: timezoneValue,
      },
      latitude: latitude,
      longitude: longitude,
    };
    const res = await saveLocation(data);
    if (!res.success) {
      return toastMessage.setErrorMessage(
        res.messages ? res.messages.errors[0].message : res.message
      );
    } else {
      changeStep(3);
    }
  };

  return (
    <>
      <Hidden mdUp implementation="js">
        <div className="form-header">
          <IconButton onClick={() => history.goBack()}>
            <ArrowBackIos />
          </IconButton>
          <div className="header-text">
            {title && <h1 dangerouslySetInnerHTML={{ __html: title }} />}
          </div>
        </div>
      </Hidden>
      <form onSubmit={(e) => submit(e)}>
        <div className="fields">
          <div className="col-10 col-sm-6 col-md-12 mb-5">
            <Map
              forBusiness
              setLatlong={(value) => setLatlong(value)}
              latlong={latlong}
            />
          </div>
          <div className="col-10 col-sm-6 col-md-12">
            <h3>{"Set your time zone"}</h3>
            <Select
              displayEmpty
              input={<OutlinedInput />}
              className="select-input"
              inputProps={{ "aria-label": "Without label" }}
              name="timezone"
              value={timezoneLabel}
              onChange={(e) => handleTimezoneValue(e)}
            >
              <MenuItem disabled value="">
                <em>{"Select time zone"}</em>
              </MenuItem>

              {timeZoneList.map((item, index) => {
                return (
                  <MenuItem key={index} value={item.label}>
                    {item.label}
                  </MenuItem>
                );
              })}
            </Select>
          </div>
          <div className="col-10 col-sm-6 col-md-12 Submit_Container">
            <UIButton className="my-5 ms-0">{"Save & Select Plan"}</UIButton>
          </div>
          <div className="col-10 col-sm-6 col-md-12">
            <small className="mini-text">
              {"Try our "}
              <strong>{"basic plan"}</strong>
              {", If you dont like our service you will get 100% Refund."}
            </small>
          </div>
        </div>
      </form>
    </>
  );
}
