import React, { useCallback, useState, useEffect } from "react";
import { Hidden } from "@material-ui/core";
import { useHistory, useLocation } from "react-router-dom";

import { useToastMessage } from "../../contexts";
import Header from "../../Components/Header/SignUpSetup";
import BasicSetup from "../../Components/NewBusiness/BasicSetup";
import LocationSetup from "../../Components/NewBusiness/LocationSetup";
import ProductSelect from "../../Components/NewBusiness/ProductSelect";
import BillingDetail from "../../Components/NewBusiness/BillingDetail";
import DirectBillingDetail from "../../Components/NewBusiness/BillingDetail/directBillingDetail";
import OrderSummary from "../../Components/NewBusiness/OrderSummary";
import { getBusinessById } from "../../api/preBusiness";

import image2 from "./assets/image-2.svg";

// styles
import useStyles from "./styles/muiStyles";

/*
  ToDo: business-setup
  ! Remove selected location address under map
  ! fix pointer in center of the map and auto select the coordinates
 */

const BusinessSetup = () => {
  const location = useLocation(),
    classes = useStyles(),
    toastMessage = useToastMessage(),
    history = useHistory(),
    [busiInfo, setBusiInfo] = useState(null),
    [editBilling, setEditBilling] = useState(false),
    [editBillingStep, setEditBillingStep] = useState(4),
    [nextStep, setNextStep] = useState(1),
    [plans, setPlans] = useState([]),
    [showOrderSummary, setShowOrderSummary] = useState(false),
    [title, setTitle] = useState(
      "Sign Up Successful ! <br />Enter your business details to start your journey."
    ),
    changeStep = useCallback(
      (next) => {
        setNextStep(next);
        if (next === 2) {
          setTitle("Enter your business details <br />to start your journey.");
        }

        //Todo: Product Selection
        if (next === 3) {
          setTitle("Select a product that suits your needs.");
        }

        if (next === 3) {
          history.push({
            pathname: `/choose-plan`,
          });
        }

        if (next === 4) {
          setTitle("Billing Details");
        }

        if (next === 5) {
          setShowOrderSummary(true);
          setTitle("Order Summary");
        }
      },
      [history]
    ),
    getBusinessDetail = useCallback(
      async (businessId) => {
        const res = await getBusinessById();
        if (!res.success) {
          return toastMessage.setErrorMessage(
            res.message || res.messages.errors[0].message
          );
        } else {
          await setBusiInfo(res.payload);
        }
      },
      [toastMessage]
    );

  useEffect(() => {
    if (location.state && location.state.from === "choose-plan") {
      history.replace({ state: {} });
      changeStep(4);
    }
    const businessId = localStorage.getItem("businessId");
    if (businessId) {
      getBusinessDetail(businessId);
    }
  }, [changeStep, history, location]);

  useEffect(() => {
    const businessId = localStorage.getItem("businessId");
    if (businessId && (nextStep === 4 || nextStep === 6)) {
      getBusinessDetail(businessId);
    }
  }, [nextStep]);

  // when redirect form plan page
  useEffect(() => {
    /* let signUpType;
    if (localStorage.getItem("selectedPlanDetail") !== null) {
      let planDetail = JSON.parse(localStorage.getItem("selectedPlanDetail"));
      setPlans(planDetail);
      signUpType = planDetail[0]["redirect"];
      if (signUpType === "billing") {
        changeStep(6);
        setEditBillingStep(6);
      }
    } 

 */
  }, []);

  return (
    <div className={classes["setup-process"]}>
      <Header />
      <section className={classes.setup}>
        <div className="content">
          <Hidden smDown implementation="js">
            <div className="block-left">
              <div className="block-fixed">
                <h1 dangerouslySetInnerHTML={{ __html: title }} />
                <img src={image2} alt="image2" className="block-img" />
              </div>
            </div>
          </Hidden>
          <div className="block-right">
            {nextStep === 1 && (
              <BasicSetup
                changeStep={(nextStep) => changeStep(nextStep)}
                //info={busiInfo && busiInfo}
                title={title}
              />
            )}
            {nextStep === 2 && (
              <LocationSetup
                changeStep={(nextStep) => changeStep(nextStep)}
                info={busiInfo && busiInfo}
              />
            )}
            {/*
              Todo: add a new step in between
            */}
            {false && (
              <ProductSelect
                changeStep={(nextStep) => changeStep(nextStep)}
                info={busiInfo && busiInfo.basicInfo}
                title={title}
              />
            )}
            {nextStep === 4 && (
              <BillingDetail
                changeStep={(nextStep) => changeStep(nextStep)}
                info={busiInfo && busiInfo.billing_detail}
                businessInfo={busiInfo && busiInfo}
                setBusiInfo={(data) => setBusiInfo(data)}
              />
            )}
            {nextStep === 5 && (
              <OrderSummary
                changeStep={(nextStep) => changeStep(nextStep)}
                open={showOrderSummary}
                onClose={() => setShowOrderSummary(false)}
                editBillingStep={editBillingStep}
                editBilling={() => setEditBilling(true)}
                busiInfo={busiInfo}
              />
            )}
            {nextStep === 6 && plans.length > 0 && (
              <DirectBillingDetail
                changeStep={(nextStep) => changeStep(nextStep)}
                info={busiInfo && busiInfo.billing_detail}
                businessInfo={busiInfo && busiInfo}
                plans={plans}
                editBilling={editBilling}
              />
            )}
          </div>
        </div>
      </section>
    </div>
  );
};

export default BusinessSetup;
